import { Component, OnInit, Input } from '@angular/core';
import {Channel} from "../channel";

@Component({
  moduleId: module.id,
  selector: 'pa-channel-list-item',
  templateUrl: 'channel-list-item.component.html',
})
export class ChannelListItemComponent implements OnInit {
  @Input() item: Channel;
  // Register an output event
  constructor() { }

  ngOnInit() {
    console.log("In Channel List Item component");
    if(this.item == null) {
      console.log(' ChannelItem is NULL');
    } else {
      console.log(' ' + this.item.name + ': ' + this.item.description);
    }
  }

  onChannelItemSelected() {
    alert('Channel item selected');
  }

}
